@use './reset';

body{
    overflow: hidden;
}
.background-img{
    width: 100%;
    height: 100%;
    position: relative;
    img {
        object-fit: cover;
        width: 100%;
        max-height: 100%;
        display: block;
    }

}
.container{
    position: relative;
    width: 100%;
    height: 100vh;
}

.pod-logo{
    width: 135px;
    height: 56px;
    position: absolute;
    z-index: 5;
    top: 62px;
    position: absolute;
margin-left: auto;
margin-right: auto;
left: 0;
right: 0;
text-align: center;
}

.text-container{
    position: absolute;
    z-index: 4;
    width: 100%;
    background-color: rgb(18, 23, 37,.8);
    .text-wrapper{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}